*{
  font-family: 'Open Sans', sans-serif;
}

.container{
  margin: auto;
}

@media (min-width: 981px){
  .container{
    width: 80%;
    max-width: 1280px;
  }
}

@media (max-width: 980px){
  .container{
    width: 90%;
  }
}


.header{
  height: 180px;
  background-color: #061123;
  margin: auto;
  color: white;
  -ms-flex-line-pack:center;
      align-content:center;
  -ms-flex-pack: center;
      justify-content: center;
}
.site-title-wrapper{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
 }
.site-title{
  color:white;
  text-align: center;
  font-size: 3rem;
  margin-bottom: 0px;
}
.site-subtitle{
  color: white;
}


.top-nav{
  width: 100%;
  height: auto;
  background-color: #020916;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-direction: row;
      flex-direction: row;
}
.top-nav .links{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.top-nav ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.top-nav ul li{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
}
.top-nav ul a{
  color: white;
  text-decoration: none;
  padding: .5rem .8rem;
  font-size: 1.1rem;
  -webkit-transition: all ease-out .15s;
  -o-transition: all ease-out .15s;
  transition: all ease-out .15s;
}
.top-nav ul a:hover{
  background-color: rgba(255, 255, 255, 0.2);
}



